<template>
  <!-- LOGIN FORM -->
  <!--===================================================-->
  <div class="login-content">
      <div class="cls-content panel">
        <div class="mar-ver pad-btm">
            <h3 style="font-size:20px;">I Share</h3>
        </div>
        <form>
            <div class="form-group">
                <el-input v-model="username" placeholder="请输入用户名"></el-input>
            </div>
            <div class="form-group">
                <el-input v-model="password" placeholder="请输入密码" type="password"></el-input>
            </div>
            <div class="checkbox pad-btm text-left">
                <el-checkbox v-model="checked">记住密码</el-checkbox>
            </div>
            <el-button type="primary" style="width:230px;" @click="login">登 录</el-button>
        </form>
        <div class="pad-all">
            <a href="javascript:;" @click="forgetPass" class="btn-link mar-rgt">忘记密码 ?</a>
            <a href="javascript:;" @click="registerAccount" class="btn-link mar-lft">注册帐号</a>
        </div>
      </div>
  </div>
  <!--===================================================-->
</template>

<script>
import configUrl from './apiConfig'
import utils from '../helper/utils'

export default {
    data () {
        return {
            username: '',
            password: '',
            checked: false,
            cacheLocation: {}
        }
    },
    beforeRouteEnter(to, from, next) {
        let localStorage = window.localStorage;
        next(vm => {
            // 通过 `vm` 访问组件实例
            if (localStorage.getItem('login')) {
                vm.$router.push({name: 'map'})
            }
        })
    },
  methods: {
    login() {
        this.$http.post(configUrl.user + '/signin', {
            username: this.username,
            ak: this.password
        }).then(res => {
            let data = res.data
            console.log(res.data)
            if (data.status == 200) {
                let msg = JSON.parse(data.msg)
                //set common header
                this.$http.defaults.headers.common['token'] = msg.token;
                console.log(msg)
                utils.setLocalStorage('login', true)
                utils.setLocalStorage('userId', msg.userId)
                this.getUserGroup(msg.userId);
                this.refreshLocationByTime();
                //if login success
                this.$emit('loginSuccess');
            } else {
                this.$alert(data.msg, '登录失败', {
                    confirmButtonText: '确定',
                    callback: action => {
                        
                    }
                });
            }

        })
        .catch(err => {
            console.log(err)
        })
    },
    getUserGroup(userId) {
        let category = 2;
        this.$http.get(configUrl.userGroup + `/${userId}/${category}`)
        .then(res => {
            let data = res.data
            if (data.status == 200) {
                let msg = JSON.parse(data.msg)
                let groupId = msg.groupId;
                let localStorage = window.localStorage;
                localStorage.setItem('groupId', groupId);
            }
            console.log(res)
        }).catch(err => {
            console.log(err)
        })
    },
    updateLocation(latitude, longitude) {
        let me = this
        this.$http.post(configUrl.location, {
            userId: utils.getLocalStorage('userId'),
            latitude,
            longitude,
            coordinateType: 1
        }).then(res => {
            me.$emit()
            console.log('location', res.data);
        }).catch(err => {
            console.log(err);
        })
    },
    getLocation() {
        let me = this
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition((geoposition) => {
                let {latitude, longitude} = geoposition.coords
                me.cacheLocation = {latitude, longitude}
                me.updateLocation(latitude, longitude);
            }, (err) => {
                console.log(err)
            })
        }
    },
    refreshLocationByTime() {
        let me = this
        if (navigator.geolocation) {
            navigator.geolocation.watchPosition((geoposition) => {
                let {latitude, longitude} = geoposition.coords
                if (!me.cacheLocation) {
                    //first should center the position
                    me.$('centerMap', geoposition.coords)
                }
                let {cacheLat, cachelnt} = me.cacheLocation
                if ({cacheLat, cachelnt} == {latitude, longitude}) {
                    return;
                }
                me.cacheLocation = {latitude, longitude}
                me.updateLocation(latitude, longitude);
            }, (err) => {
                console.log(err)
            });
        }
    },
    forgetPass() {
        this.$emit('forgetPass')
    },
    registerAccount() {
        this.$emit('registerAccount')
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  a {
    text-decoration: none;
    color: #758697;
    outline: 0;
  }
  .el-message-box  {
      width: 70%;
  }
  .login-content{
    padding: 20px 15px 15px;
    position: relative;
    
    .cls-content {
        width: 70%;
        min-width: 270px;
        margin: 0 auto;
        position: relative;
        background-color: transparent;
        border: 0;
        box-shadow: none;

        .el-checkbox__label {
          color: #758697;
        }

        .btn-link {
            font-weight: 400;
            border-radius: 0;
        }

        .text-left {
            text-align: left;
        }

        .pad-btm {
            padding-bottom: 15px;
        }
        .mar-ver {
            margin-top: 15px;
            margin-bottom: 15px;
        }

        .form-group {
            margin-bottom: 15px;

            .form-control {
            font-size: 13px;
            height: 100%;
            border-radius: 0;
            box-shadow: none;
            border: 1px solid #e9e9e9;
            transition-duration: .5s;
        }
        }

        .checkbox{
            position: relative;
            display: block;
            margin-top: 10px;
            margin-bottom: 10px;
        }

        .pad-all {
          padding: 15px;

          a {
              font-size: 12px;
          }
        }
    }
  }
</style>



